@import 'colors.scss';
section {
  height: auto;

  .contact-section {
    color: white;
    height: auto;
    padding-top: 1px;
    display: flex; 
    flex-direction: column;
    align-items: center;
    font-family: "proxima-nova",sans-serif;

    .contact-form {
      // color: white;
      display: flex; 
      flex-direction: column;
      align-items: center;
      width: 100%;
    }
    h1 {
      text-align: center;
      text-decoration: underline solid 3px;
      font-size: 24px;
      color: white;
    }
    .contact-grid {
      margin-top: 10px;
      display: flex;
      position: relative; 
      justify-content: space-evenly;
      width: 100%;
      padding-left: 0;
      a {
        text-align: center;
        text-decoration: none;
        color: white;
        font-weight: bold;
        &:hover {
          opacity: 0.5;
        }
      }
      li {
        text-decoration: none;
        list-style-type: none;
        p {
          text-align: center;
        }
        img {
          margin-top: 10%;
          display: block;
          height: 75px;
          width: 75px;
          margin-left: auto;
          margin-right: auto;
        }     
      }
    }
    // textarea {
