    //   }
    // }
  
    // input[type=text] {
    //   padding: 15px 0;
    //   font-size: 24px;
    //   padding-left: 10px;
    // }
  }
}

@media (max-width: 800px) {
  section {
    .contact-section {
      height: auto;
      .resume-doc {
        display: none;
      }
      .contact-grid {
        flex-direction: column;
      }
    }
  }
}